import { workStatusDict, workTypeDict, roleTypeDict, farmingTypeDict } from '@/utils/dict'

/**
 * 
 */
export const workHandleDict = {
	[workStatusDict.waitExecute]: {
		[workTypeDict.farming]: '',
		[workTypeDict.pick]: '',
		[workTypeDict.productPackaging]: '',
		[workTypeDict.secondaryPackaging]: '',
		[workTypeDict.qualityTesting]: ''
	},
	[workStatusDict.execute]: {
		[workTypeDict.farming]: '',
		[workTypeDict.pick]: '',
		[workTypeDict.productPackaging]: '',
		[workTypeDict.secondaryPackaging]: ''
	},
	[workStatusDict.complete]: {
		[workTypeDict.secondaryPackaging]: '',
		[workTypeDict.pick]: '',
	}
}


/**
 * 
 */

export const getWorkRenderList = (workType, workStatus, roleType, farmingType) => {
	switch (workType) {
		// 
		case workTypeDict.farming:
			switch (workStatus) {
				case workStatusDict.waitExecute:
				case workStatusDict.overdue:
					return {
						rows: [
							{ label: '', key: 'workNo', type: 'text' },
							{ label: '', key: 'executorName', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'estimateEndTime', type: 'text', filterType: "date" }
						]
					}
				case workStatusDict.execute:
					return {
						rows: [
							{ label: '', key: 'workNo', type: 'text' },
							{ label: '', key: 'executorName', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'estimateEndTime', type: 'text', filterType: "date" },
							{ label: '', key: 'address', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'actualStartTime', type: 'text', filterType: "date" },
							{ label: '', key: 'teaGardenCode', type: 'code' }
						]
					}
				case workStatusDict.complete:
					return {
						rows: [
							{ label: '', key: 'workNo', type: 'text' },
							{ label: '', key: 'executorName', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'estimateEndTime', type: 'text', filterType: "date" }
						],
						panelText: "",
						panelRender: [
							{ label: '', key: 'address', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'actualStartTime', type: 'text', filterType: "date" },
							{ label: '', key: 'actualEndTime', type: 'text', filterType: "date" },

							{ label: '', key: 'farmingProcessType', type: 'text', isHide: farmingType != farmingTypeDict.weeding },
							{ label: '', key: 'remark', type: 'textarea', isHide: farmingType != farmingTypeDict.weeding, emptyHide: true },

							{ label: '', key: 'farmingProcessType', type: 'text', isHide: farmingType != farmingTypeDict.turnTheSoil },
							{ label: '', key: 'remark', type: 'textarea', isHide: farmingType != farmingTypeDict.turnTheSoil, emptyHide: true },

							{ label: '', key: 'farmingProcessType', type: 'text', isHide: farmingType != farmingTypeDict.clipTrim },
							{ label: '', key: 'pruneTool', type: 'text', isHide: farmingType != farmingTypeDict.clipTrim, emptyHide: true },
							{ label: '', key: 'remark', type: 'textarea', isHide: farmingType != farmingTypeDict.clipTrim, emptyHide: true },

							{ label: '', key: 'farmingProcessType', type: 'text', isHide: farmingType != farmingTypeDict.applyFertilizer },
							{ label: '', key: 'fertilizerName', type: 'text', isHide: farmingType != farmingTypeDict.applyFertilizer, emptyHide: true },
							{ label: '', key: 'dosage', type: 'text', isHide: farmingType != farmingTypeDict.applyFertilizer, emptyHide: true, suffixText: 'kg' },
							{ label: '', key: 'remark', type: 'textarea', isHide: farmingType != farmingTypeDict.applyFertilizer, emptyHide: true },

							{ label: '', key: 'pesticideName', type: 'text', isHide: farmingType != farmingTypeDict.medicine },
							{ label: '', key: 'dosage', type: 'text', isHide: farmingType != farmingTypeDict.medicine, emptyHide: true, suffixText: 'kg' },
							{ label: '', key: 'remark', type: 'textarea', isHide: farmingType != farmingTypeDict.medicine, emptyHide: true },

							{ label: '', key: 'farmingProcessType', type: 'text', isHide: farmingType != farmingTypeDict.irrigation },
							{ label: '', key: 'irrigateWaterSource', type: 'text', isHide: farmingType != farmingTypeDict.irrigation, emptyHide: true },
							{ label: '', key: 'remark', type: 'textarea', isHide: farmingType != farmingTypeDict.irrigation, emptyHide: true },

							{ label: '', key: 'teaGardenCode', type: 'code' },
							{ label: '', key: 'imageUrl', type: 'img' },
						]
					}
			}
			break
		// 
		case workTypeDict.pick:
			switch (workStatus) {
				case workStatusDict.waitExecute:
				case workStatusDict.overdue:
					return {
						rows: [
							{ label: '', key: 'workNo', type: 'text' },
							{ label: '', key: 'executorName', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'estimateEndTime', type: 'text', filterType: "date" },
							{ label: '', key: 'pickBreed', type: 'text' },
							{ label: '', key: 'pickRequire', type: 'text' }
						]
					}
				case workStatusDict.execute:
					return {
						rows: [
							{ label: '', key: 'workNo', type: 'text' },
							{ label: '', key: 'executorName', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'estimateEndTime', type: 'text', filterType: "date" },
							{ label: '', key: 'pickBreed', type: 'text' },
							{ label: '', key: 'pickRequire', type: 'text' },
							{ label: '', key: 'address', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'actualStartTime', type: 'text', filterType: "date" },
							{ label: '', key: 'teaGardenCode', type: 'code' }
						]
					}
				case workStatusDict.complete:
					return {
						rows: [
							{ label: '', key: 'pickBatchCode', type: 'text' },
							{ label: '', key: 'executorName', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'estimateEndTime', type: 'text', filterType: "date" },
							{ label: '', key: 'pickBreed', type: 'text' },
							{ label: '', key: 'pickRequire', type: 'text' }
						],
						panelText: "",
						panelRender: [
							{ label: '', key: 'address', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'actualStartTime', type: 'text', filterType: "date" },
							{ label: '', key: 'actualEndTime', type: 'text', filterType: "date" },
							{ label: '', key: 'teaGardenCode', type: 'code' },
							{ label: '', key: 'imageUrl', type: 'img' },
						]
					}
			}
			break
		// 
		case workTypeDict.production:
			switch (workStatus) {
				case workStatusDict.waitExecute:
				case workStatusDict.overdue:
					return {
						rows: [
							{ label: '', key: 'workNo', type: 'text' },
							{ label: '', key: 'executorName', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'estimateEndTime', type: 'text', filterType: "date" },
						]
					}
				case workStatusDict.execute:
					return {
						rows: [
							{ label: '', key: 'workNo', type: 'text' },
							{ label: '', key: 'executorName', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'estimateEndTime', type: 'text', filterType: "date" },
							{ label: '', key: 'productBatchCode', type: 'text' },
							{ label: '', key: 'actualStartTime', type: 'text', filterType: "date" },
							{ label: '', key: '', type: 'text' },
						]
					}
				case workStatusDict.complete:
					return {
						rows: [
							{ label: '', key: 'workNo', type: 'text' },
							{ label: '', key: 'executorName', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'estimateEndTime', type: 'text', filterType: "date" },
							{ label: '', key: 'productBatchCode', type: 'text' },
							{ label: '', key: 'actualStartTime', type: 'text', filterType: "date" },
							{ label: '', key: '', type: 'text' },
							{ label: '', key: 'actualEndTime', type: 'text', filterType: "date" },
							{ label: '', key: '', type: 'text' },
						]
					}
			}
			break
		case workTypeDict.qualityTesting:
			switch (workStatus) {
				case workStatusDict.waitExecute:
				case workStatusDict.overdue:
					return {
						rows: [
							{ label: '', key: 'workNo', type: 'text' },
							{ label: '', key: 'executorName', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'estimateEndTime', type: 'text', filterType: "date" },
						]
					}
				case workStatusDict.complete:
					return {
						rows: [
							{ label: '', key: 'workNo', type: 'text' },
							{ label: '', key: 'executorName', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'estimateEndTime', type: 'text', filterType: "date" },
							{ label: '', key: 'productBatchCode', type: 'text' },
							{ label: '', key: 'actualEndTime', type: 'text', filterType: "date" },
							{ label: '', key: 'qualityReport', type: 'img' },
						]
					}
			}
			break
		case workTypeDict.productPackaging:
			switch (workStatus) {
				case workStatusDict.waitExecute:
				case workStatusDict.overdue:
					return {
						rows: [
							{ label: '', key: 'workNo', type: 'text' },
							{ label: '', key: 'executorName', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'estimateEndTime', type: 'text', filterType: "date" },
						]
					}
				case workStatusDict.execute:
					return {
						rows: [
							{ label: '', key: 'workNo', type: 'text' },
							{ label: '', key: 'executorName', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'estimateEndTime', type: 'text', filterType: "date" },
							{ label: '', key: 'productBatchCode', type: 'text' },
							{ label: '', key: 'idscodeNum', type: 'text' }
						]
					}
				case workStatusDict.complete:
					return {
						rows: [
							{ label: '', key: 'workNo', type: 'text' },
							{ label: '', key: 'executorName', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'estimateEndTime', type: 'text', filterType: "date" },
							{ label: '', key: 'productBatchCode', type: 'text' },
							{ label: '', key: 'actualEndTime', type: 'text', filterType: "date" },
							{ label: '', key: 'idscodeNum', type: 'text' }
						],
						panelText: "",
						panelRender: [
							{ label: '', key: 'idsCodeList', type: 'codes' }
						]
					}
			}
			break
		case workTypeDict.secondaryPackaging:
			switch (workStatus) {
				case workStatusDict.waitExecute:
				case workStatusDict.overdue:
					return {
						rows: [
							{ label: '', key: 'workNo', type: 'text' },
							{ label: '', key: 'executorName', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'estimateEndTime', type: 'text', filterType: "date" },
						]
					}
				case workStatusDict.execute:
					return {
						rows: [
							{ label: '', key: 'workNo', type: 'text' },
							{ label: '', key: 'executorName', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'estimateEndTime', type: 'text', filterType: "date" },
							// { label: '', key: 'productBatchCode', type: 'text' },
							{ label: '', key: 'packageNum', type: 'text' },
							{ label: '', key: 'idscodeNum', type: 'text' },
						]
					}
				case workStatusDict.complete:
					return {
						rows: [
							{ label: '', key: 'workNo', type: 'text' },
							{ label: '', key: 'executorName', type: 'text', isHide: roleType != roleTypeDict.enterprise },
							{ label: '', key: 'estimateEndTime', type: 'text', filterType: "date" },
							// { label: '', key: 'productBatchCode', type: 'text' },
							{ label: '', key: 'actualEndTime', type: 'text', filterType: "date" },
							{ label: '', key: 'packageNum', type: 'text' },
							{ label: '', key: 'idscodeNum', type: 'text' },
						]
					}
			}
			break
	}
}


